import React, { useState, useEffect } from "react";
import { lighten, makeStyles } from "@material-ui/core/styles";
import Typography from "@material-ui/core/Typography";
import Select from "@material-ui/core/Select";
import {
  MenuItem,
  TextField,
  TableRow,
  FormControl,
  Button,
  TableBody,
} from "@material-ui/core";
import { TextArea } from "grommet";
const axios = require("axios");

export default function InjectionPod(props) {
  const [services, setServices] = useState([]);

  function fetchServices() {
    axios
      .post("/queryservices", {}, corsOptions)
      .then((response) => {
        console.log(response.data);
        setServices(response.data);
      })
      .catch((error) => {
        console.log(error);
      });
  }

  // const destinations = props.destinations;
  const dest = props.destination;

  const [id, setID] = React.useState("0123211");
  const [type, setType] = React.useState("test.type");
  const [source, setSource] = React.useState("test.source");
  const [contenttype, setContenttype] = React.useState("application/json");
  const [data, setData] = React.useState('{"test":"data"}');
  const [destination, setDestination] = React.useState(dest);

  const divStyle = {
    width: "380px",
    high: "100px",
  };

  const corsOptions = {
    origin: "*",
  };

  const useToolbarStyles = makeStyles((theme) => ({
    root: {
      paddingLeft: theme.spacing(0),
      paddingRight: theme.spacing(1),
    },
    highlight:
      theme.palette.type === "light"
        ? {
            color: theme.palette.secondary.main,
            backgroundColor: lighten(theme.palette.secondary.light, 0.85),
          }
        : {
            color: theme.palette.text.primary,
            backgroundColor: theme.palette.secondary.dark,
          },
    title: {
      flex: "1 1 100%",
      paddingLeft: 8,
    },
  }));

  const classes = useToolbarStyles();

  const handleInjection = (event) => {
    axios
      .post(
        "/inject",
        {
          destination,
          data,
          headers: {
            "Ce-Id": id,
            "Ce-Specversion": "1.0",
            "Ce-Type": type,
            "Ce-Source": source,
            "Content-Type": contenttype,
          },
        },
        corsOptions
      )
      .then(function (response) {
        console.log(response);
      })
      .catch(function (error) {
        console.log(error);
      });
  };

  return (
    <TableBody class="pure-form">
      <TableRow class="pure-form">
        <FormControl>
          ID:
          <TextField
            class="pure-form"
            id="input-injection-ceid"
            value={id}
            onChange={(e) => setID(e.target.value)}
          />
        </FormControl>
      </TableRow>
      <TableRow>
        <FormControl>
          Type
          <TextField
            class="pure-form"
            id="input-injection-type"
            value={type}
            onChange={(e) => setType(e.target.value)}
          />
        </FormControl>
      </TableRow>
      <TableRow>
        Source
        <FormControl class="pure-form">
          <TextField
            id="input-injection-source"
            value={source}
            onChange={(e) => setSource(e.target.value)}
          />
        </FormControl>
      </TableRow>
      <TableRow>
        Content-Type
        <FormControl class="pure-form">
          <TextField
            label=""
            id="input-injection-contenttype"
            value={contenttype}
            onChange={(e) => setContenttype(e.target.value)}
          />
        </FormControl>
      </TableRow>
      <p1 class="pure-form"> Avalible Destinations: </p1>
      <TableRow>
        <FormControl style={{ width: "190px" }}>
          <Select onChange={(e) => setDestination(e.target.value)}>
            {services.map((data, index) => (
              <MenuItem value={data}>{data}</MenuItem>
            ))}
          </Select>
        </FormControl>
      </TableRow>
      <TableRow>
        <Button
          // style={{ height: 40, width: 200, marginLeft: "85px" }}
          onClick={fetchServices}
        >
          Refresh Destinations
        </Button>
      </TableRow>
      <p1 class="pure-form"> Custom Destination: </p1>
      <TableRow>
        <FormControl class="pure-form">
          <TextField
            id="input-injection-add"
            onChange={(e) => setDestination(e.target.value)}
          />
        </FormControl>
      </TableRow>
      <TableRow>
        <FormControl class="pure-form">
          Data:
          <TextArea
            id="input-injection-data"
            value={data}
            onChange={(e) => setData(e.target.value)}
          />
        </FormControl>
      </TableRow>
      <TableRow>
        <FormControl class="pure-form">
          <Button
            id="input-injection-button"
            label="Submit"
            onClick={handleInjection}
          >
            Send
          </Button>
        </FormControl>
      </TableRow>
    </TableBody>
  );
}
